import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { useData } from '../components/DataLoader';
import './Tools.css';

function Tools() {
  const { data, loading, error } = useData();

  if (loading) return <Container className="my-5"><h2>Chargement...</h2></Container>;
  if (error) return <Container className="my-5"><h2>Erreur de chargement</h2></Container>;
  if (!data.tools || data.tools.length === 0) return <Container className="my-5"><h2>Aucun outil disponible</h2></Container>;

  return (
    <Container className="my-5">
      <h1>Nos Outils</h1>
      <br />
      <div className="tools-container">
        {data.tools.map((tool, index) => (
          <Card key={tool.id} className="tool-card mb-4">
            <Row className={`g-0 ${index % 2 === 1 ? 'flex-row-reverse' : ''}`}>
              <Col md={4} className="tool-image-container align-items-center">
                <div className="tool-image-wrapper">
                    
                  <img
                    src={process.env.PUBLIC_URL + '/images/'+ tool.image_url}
                    alt={tool.nom}
                    className="tool-image"
                    style={{ height: '256px', width: 'auto' }}
                  />
                </div>
              </Col>
              <Col md={8}>
                <Card.Body className="tool-content">
                  <Card.Title className="tool-title">{tool.nom}</Card.Title>
                  <Card.Text className="tool-description">
                    {tool.description}
                  </Card.Text>
                  <a 
                    href={tool.lien} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="tool-link"
                  >
                    Télécharger l'outil
                  </a>
                </Card.Body>
              </Col>
            </Row>
          </Card>
        ))}
      </div>
    </Container>
  );
}

export default Tools;